<template>
  <div class="tip" @click="clickHandler">
    防骗小贴士>>>
  </div>
</template>

<script>
export default {
  methods: {
    clickHandler () {
      this.$emit('tap')
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/theme'

  .tip
    position: absolute
    cursor: pointer
    z-index: 10000
    background-color: $text-color
    color: #fff
    bottom: 10px
    left: 50%
    padding: 8px
    font-size: $font-size-small-s
    border-radius: 3px
    box-shadow:0px 1px 5px #5E63B6
    transform: translate(-50%, 0)
    font-size: $font-size-large-xxl
    min-width: 170px

  @media screen and (max-width: 480px)
    .tip
      font-size: $font-size-large-xl
</style>



