<template>
  <transition name="drop">
    <div class="result-wrapper">
      <scroll :data="result">
        <div class="all-result">
          <div class="result" v-for="(item, index) in result" :key="index">
            <p class="title">最适合的小伙伴{{ titleIndex(index + 1) }}: </p>
            <desc-item text="姓名" :value="item.name"/>
            <desc-item text="个人技能" :value="item.skill"/>
            <desc-item text="手机号码" :value="item.phTelphone"/>
            <desc-item text="联系方式" :value="item.call"/>
            <desc-item text="匹配度" :value="item.value"/>
          </div>
        </div>
        <spots/>
      </scroll>
      <tip @tap="tapHandler"/>
    </div>
  </transition>
</template>

<script>
import Desc from 'base/desc/desc'
import Spots from 'base/spots/spots'
import Scroll from 'base/scroll/scroll'
import Tip from 'base/tip/tip'

export default {
  data () {
    return {
      result: this.$eventBus.result
    }
  },
  created () {
    this.back()
  },
  methods: {
    back () {
      if (this.result === null) {
        this.$router.back()
      }
    },
    tapHandler () {
      this.$router.push({
        path: '/tips'
      })
    },
    titleIndex (index) {
      return this.result.length > 1 ? index : ''
    }
  },
  components: {
    DescItem: Desc,
    Spots,
    Scroll,
    Tip
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/theme'

  .drop-enter-active
    transition: transform .4s cubic-bezier(0.86, 0.18, 0.82, 2.1)
  .drop-enter
    transform: translate3d(0, -300px, 0)

  .result-wrapper
    position: relative
    width: 100%
    height: 100%
    cursor: move
    .all-result
      display: flex
      align-items: center
      justify-content: center
      flex-direction: column
      padding: 5px 0 5px 0
      min-height: 100%
      .result
        box-sizing: border-box
        width: 750px
        margin: 15px
        padding: 20px 20px 10px 20px
        border-radius: 5px
        box-shadow:0px 1px 2px #5E63B6
        .title
          color: $text-color
          font-size: $font-size-large-xl
          font-weight: 700
          padding-bottom: 5px
  @media screen and (max-width: 768px)
    .result-wrapper
      .all-result
        .result
          width: 95%
          .title
            font-size: $font-size-large
</style>



