<template>
  <div class="spots-wrapper">
    <span
      class="spot"
      v-for="(item, index) in spots"
      :key="index"
      :style="item"
    >
    </span>
  </div>
</template>

<script>
const colors = [
  '#C91B00', '#E92224', '#FF5600', '#392673', '#E92224',
  '#392673', '#003E87', '#175AAB', '#0087E7'
]

export default {
  props: {
    size: {
      type: Number,
      default: 10
    }
  },
  data () {
    return {
      spots: []
    }
  },
  mounted () {
    this.initSpots()
  },
  methods: {
    initSpots () {
      this.$el.parentNode.style.position = 'relative'
      this.$el.parentNode.style.overflow = 'hidden'
      for (let i = 0, len = this.size; i < len; i++) {
        this.spots.push(this.getSpot(i, this.size))
      }
    },
    getSpot (index, length) {
      // 颜色
      let color = colors[parseInt(Math.random() * 10)]
      // 大小 >= 20 && < 60
      let size = parseInt(Math.random() * 10 + 10) + parseInt(Math.random() * 30 + 10)
      // 假设屏幕宽是100份，为使得位置均匀分布，得base
      let base = 100 / length
      let left = base * (index + 1)
      const style = {
        background: color,
        width: `${size}px`,
        height: `${size}px`,
        marginTop: `-${size / 2}px`,
        marginLeft: `-${size / 2}px`,
        top: `${Math.random() * 100}%`, // 水平方向均匀分布，垂直方向随机分布
        left: `${left - (base / 2)}%`
      }
      return style
    }
  }
}
</script>

<style lang="stylus" scoped>
  .spot
    position: absolute
    border-radius: 50%
    opacity: .13
</style>





