<template>
  <transition name="fadeRight">
    <div class="tips-wrapper">
      <draw @reset="resetHandler"/>
      <transition-group name="zoom" tag="ul">
        <li v-for="(item, index) in mes" :key="index"  v-show="isShow" class="item">{{ index + 1 }}. {{ item }}</li>
      </transition-group>
      <!-- <div class="back" v-show="isShow">
        <i class="iconfont icon-detail-left"></i>
      </div> -->
    </div>
  </transition>
</template>

<script>
import Draw from 'base/draw/draw'

export default {
  data () {
    return {
      mes: [
        '你要知道那个地方的房子价格（和建成年代，商圈，地铁站等有关）',
        '供暖方式，水电，燃气，尤其是供暖方式，不是集体供暖的话，可能要多花很多钱',
        '中介费要多少，可不可以刀，提前毁约扣多少押金，房屋设施损坏谁来修谁赔，而且必须体现在合同上',
        '押一付几，几号交房租，如果押一付一，会不会有拿你个人信息做贷款的风险，如果押一付三，会不会有提前收租的可能~',
        '租房最好有熟人的在一起，也适当的有个照应。如果没有也要看看同租的室友人品如何',
        '京东北京合租群64615445，对本项目有兴趣的可联系咚咚baiyuehui，jiangyusen，邮箱加@jd.com'
      ],
      isShow: false
    }
  },
  methods: {
    resetHandler () {
      this.isShow = true
    }
  },
  components: {
    Draw
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/theme'

  .tips-wrapper
    width: 100%
    height: 100%
    display: flex
    align-items: center
    justify-content: center
    flex-direction: column
    ul
      z-index: 30
      position: relative
      .item
        font-size: $font-size-large-xl
        margin: 30px
    .back
      position: fixed
      right: 10px
      bottom: 30px
      .iconfont
        font-size: 60px
        color: $text-color

    @media screen and (max-width: 480px)
      ul
        .item
          font-size: $font-size-medium-x
      .back
        .iconfont
          font-size: 20px

  .fadeRight-enter-active, .fadeRight-leave-active
    transition: all .4s
  .fadeRight-enter, .fadeRight-leave-to
    transform: translate3d(100%, 0, 0)

  .zoom-enter-active, .zoom-leave-active
    animation-duration: 1s
    animation-fill-mode: both
    animation-name: zoomIn

  @keyframes zoomIn {
    from {
      opacity: 0;
      transform: scale3d(0.3, 0.3, 0.3);
    }
    to {
      opacity: 1;
    }
  }
</style>



