<template>
  <div class="em-shape-shifter">
    <canvas ref="canvas" data-role="led-content"></canvas>
  </div>
</template>

<script>
import shapeShifter from './shape-shifter'

export default {
  data () {
    return {
      timeId: null
    }
  },
  mounted () {
    shapeShifter.init(this.$refs.canvas, 'T i p s')
    this.timeId = setTimeout(() => {
      shapeShifter.UI.reset()
      this.$emit('reset')
    }, 1000)
  },
  beforeDestroy () {
    clearTimeout(this.timeId)
  }
}
</script>

<style lang="stylus" scoped>
  .em-shape-shifter canvas
    position: absolute
    top: 0
    left: 0
    bottom: 0
    right: 0
    z-index: 1
</style>



