<template>
  <p class="desc" v-if="isShow">
    <span class="key">{{text}}:</span>
    <span class="val">{{ value }}</span>
  </p>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      default: 'xxxx'
    },
    value: {
      type: [String, Number, Boolean]
    }
  },
  computed: {
    isShow () {
      return !(this.value == null)
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/theme'

  .desc
    box-sizing: border-box
    display: flex
    align-items: center
    justify-content center
    margin: 15px
    .key
      flex: 0 0 250px
      text-align: right
      color: $text-color
      margin-right: 20px
      font-size: $font-size-large-xl
      vertical-align: top
    .val
      flex: 0 0 300px
      text-align: left
      font-size: $font-size-large-xxl
      vertical-align: top
  @media screen and (max-width: 480px)
    .desc
      margin: 8px
      .key
        flex: 0 0 120px
        font-size: $font-size-large
        margin-right: 8px
      .val
        flex: 0 0 150px
        font-size: $font-size-large-xl
</style>



