<template>
  <transition name="drop">
    <div class="empty-wrapper">
      <spots/>
      <div class="empty">
        <div class="happy">
          <i class="iconfont icon-happy"></i>
        </div>
        <p class="empty-text">没有找到与您匹配的舍友</p>
      </div>
      <tip @tap="tapHandler"/>
    </div>
  </transition>
</template>

<script>
import Spots from 'base/spots/spots'
import Tip from 'base/tip/tip'

export default {
  methods: {
    tapHandler () {
      this.$router.push({
        path: '/tips'
      })
    }
  },
  components: {
    Spots,
    Tip
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/theme'

  .drop-enter-active
    transition: transform .4s cubic-bezier(0.86, 0.18, 0.82, 2.1)
  .drop-enter
    transform: translate3d(0, -300px, 0)

  .empty-wrapper
    position: relative
    width: 100%
    height: 100%
    display: flex
    align-items: center
    justify-content: center
    flex-direction: column
    .empty
      position: relative
      margin-top: -80px
      .btn
        position: absolute
        bottom: -100px
        right: 0
      .happy
        margin-bottom: 20px
        text-align: center
        .iconfont, .icon-happy
          display: inline-block
          transform: translate3d(0, 0, 0)
          transform-origin: bottom
          animation: happy 1.5s linear 0s infinite
          font-size: 60px
          color: $text-color
      .empty-text
        color: $text-color
        font-size: $font-size-large-xxl
        text-align: center

  @media screen and (max-width: 480px)
    .empty-wrapper
      .empty
        .happy
          .iconfont
            font-size: 40px
        .empty-text
          font-size: $font-size-large-x

  @keyframes happy {
    0% {
      transform: rotate(0deg)
    }
    20% {
      transform: rotate(-20deg)
    }
    60% {
      transform: rotate(0deg)
    }
    80% {
      transform: rotate(20deg)
    }
    100% {
      transform: rotate(0deg)
    }
  }
</style>



